:root {
  --font-sans: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
  --text-primary: hsl(224 71.4% 4.1%);
  --text-secondary: hsl(215 20.2% 65.1%);
  --bg-primary: hsl(0 0% 100%);
  --bg-secondary: hsl(220 13% 91%);
  --border-color: hsl(215 20.2% 65.1%);
  --primary-color: #288185;
  --primary-color-dark: #1e686b;
  --today-bg: #eff6ff;
  --event-bg: #288185;
  --event-selected-bg: #1e686b;
  --event-own-bg: #ef4444;
  --event-other-bg: #f97316;
}

/* Custom Event Colors via eventPropGetter */
/* Increased specificity for event category colors */
.rbc-event.rbc-event-category-coordinador { background-color: #F59E0B !important; }
.rbc-event.rbc-event-category-entrevista { background-color: #8B5CF6 !important; }
.rbc-event.rbc-event-category-activitats { background-color: #10B981 !important; }
.rbc-event.rbc-event-category-reunions { background-color: #3B82F6 !important; }
.rbc-event.rbc-event-category-jav { background-color: #84CC16 !important; }
.rbc-event.rbc-event-category-calendari { background-color: #FACC15 !important; }
.rbc-event.rbc-event-category-csi { background-color: #EF4444 !important; }
.rbc-event.rbc-event-category-eib { background-color: #22D3EE !important; }
.rbc-event.rbc-event-category-fb { background-color: #6366F1 !important; }
.rbc-event.rbc-event-category-proves { background-color: #EC4899 !important; }

.rbc-calendar {
  font-family: var(--font-sans);
  color: var(--text-primary);
  background-color: var(--bg-primary);
  border: 1px solid var(--border-color);
  border-radius: 0.5rem;
  padding: 1rem;
}

/* Toolbar is hidden, using custom one */
.rbc-toolbar {
  display: none !important;
}

/* General Header Styles */
.rbc-header {
  background-color: var(--bg-secondary);
  border: none;
  padding: 0.75rem 0.5rem;
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--text-secondary);
  text-align: center;
}

.rbc-header + .rbc-header {
  border-left: 1px solid var(--border-color);
}

/* Today's Date Highlight */
.rbc-today {
  background-color: var(--today-bg) !important;
}

/* Month View */
.rbc-month-view {
  border: 1px solid var(--border-color); /* Main outer border */
}

.rbc-month-header {
  border-bottom: 1px solid var(--border-color);
}

.rbc-month-row {
  border: none; /* Remove all borders from month row */
}

.rbc-month-row + .rbc-month-row {
  border-top: 1px solid var(--border-color); /* Only top border between rows */
}

.rbc-date-cell {
  padding: 0.5rem;
  text-align: left;
  border-top: 1px solid var(--border-color) !important;
  border-left: 1px solid var(--border-color) !important;
  border-right: 1px solid var(--border-color) !important;
  border-bottom: none !important; /* No bottom border for rbc-date-cell */
}

.rbc-date-cell:first-child {
    border-left: none !important; /* Ensure no left border for the first cell in a row */
}

/* rbc-row-segment borders */
.rbc-row-segment {
  border-top: none !important; /* No top border for rbc-row-segment */
  border-left: 1px solid var(--border-color) !important;
  border-right: 1px solid var(--border-color) !important;
  border-bottom: 1px solid var(--border-color) !important;
}

/* Ensure the last rbc-date-cell and rbc-row-segment in a row have a right border */
.rbc-month-row .rbc-date-cell:last-child,
.rbc-month-row .rbc-row-segment:last-child {
    border-right: 1px solid var(--border-color) !important;
}

/* Ensure the first rbc-date-cell and rbc-row-segment in a row have a left border */
.rbc-month-row .rbc-date-cell:first-child,
.rbc-month-row .rbc-row-segment:first-child {
    border-left: 1px solid var(--border-color) !important;
}

/* Remove the left border for the first rbc-date-cell and rbc-row-segment in a row if it's the first column */
.rbc-month-row .rbc-date-cell:nth-child(7n+1),
.rbc-month-row .rbc-row-segment:nth-child(7n+1) {
    border-left: none !important;
}

.rbc-row-bg .rbc-day-bg {
    border-left: 1px solid var(--border-color) !important; /* Ensure day backgrounds have left border */
}

.rbc-row-bg .rbc-day-bg:first-child {
    border-left: none !important; /* Remove left border for the first day background */
}


.rbc-date-cell .rbc-button-link {
  color: var(--text-primary);
  font-size: 0.875rem;
}

.rbc-off-range-bg {
  background-color: var(--bg-secondary);
}

/* Week & Day Views */
.rbc-time-view {
  border: 1px solid var(--border-color); /* Consistent border for time views */
}

.rbc-time-header {
  border-bottom: 1px solid var(--border-color);
}

.rbc-time-header-content {
  border-left: none;
  min-height: 0 !important; /* This should fix the extra height */
}

.rbc-allday-cell {
    display: none !important; /* This will hide the all-day events row */
}

.rbc-time-header-gutter {
  border-left: none;
}

.rbc-time-gutter {
  font-size: 0.75rem;
  color: var(--text-secondary);
  border-right: 1px solid var(--border-color);
}

.rbc-day-slot .rbc-time-slot {
  border-top: 1px dotted var(--border-color);
}

.rbc-day-slot .rbc-event {
    border: 1px solid var(--primary-color-dark);
}

/* Agenda View */
.rbc-agenda-view {
  border: 1px solid var(--border-color);
  border-radius: 0.5rem;
  overflow: hidden;
}

.rbc-agenda-view table {
  border: none;
}

.rbc-agenda-view table thead th {
  background-color: var(--bg-secondary);
  border-bottom: 1px solid var(--border-color);
  padding: 0.5rem 0.75rem; /* Reduced padding */
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--text-secondary);
}

.rbc-agenda-view table tbody tr td {
  padding: 0.5rem 0.75rem; /* Reduced padding */
  border-bottom: 1px solid var(--border-color);
}

.rbc-agenda-view table tbody tr:last-child td {
  border-bottom: none;
}

.rbc-agenda-date-cell, .rbc-agenda-time-cell {
    font-weight: 500;
    color: var(--text-primary);
    font-size: 0.875rem; /* Smaller font size */
}

.rbc-agenda-event-cell {
    font-size: 0.875rem; /* Smaller font size */
}

/* Events */
.rbc-event {
  color: white;
  border-radius: 0.25rem;
  padding: 0.25rem 0.5rem;
  font-size: 0.875rem;
}

.rbc-event-custom {
    /* background-color: var(--event-bg); */
}

.rbc-event.rbc-selected {
  background-color: var(--event-selected-bg);
}

.rbc-event-incident-own {
  background-color: var(--event-own-bg);
  border-color: var(--event-own-bg);
}

.rbc-event-incident-other {
  background-color: var(--event-other-bg);
  border-color: var(--event-other-bg);
}
