@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  :root {
    --background: 0 0% 100%;
    --foreground: 224 71.4% 4.1%;
    --card: 0 0% 100%;
    --card-foreground: 224 71.4% 4.1%;
    --popover: 0 0% 100%;
    --popover-foreground: 224 71.4% 4.1%;
    --primary: 183 53% 34%;
    --primary-foreground: 0 0% 100%;
    --secondary: 220 13% 91%;
    --secondary-foreground: 224 71.4% 4.1%;
    --muted: 220 13% 91%;
    --muted-foreground: 215 20.2% 65.1%;
    --accent: 220 13% 91%;
    --accent-foreground: 224 71.4% 4.1%;
    --destructive: 0 84.2% 60.2%;
    --destructive-foreground: 0 0% 100%;
    --border: 215 20.2% 65.1%;
    --input: 215 20.2% 65.1%;
    --ring: 183 53% 34%;
    --radius: 0.5rem;
  }

  .dark {
    --background: 224 71.4% 4.1%;
    --foreground: 0 0% 100%;
    --card: 224 71.4% 4.1%;
    --card-foreground: 0 0% 100%;
    --popover: 224 71.4% 4.1%;
    --popover-foreground: 0 0% 100%;
    --primary: 183 53% 34%;
    --primary-foreground: 0 0% 100%;
    --secondary: 215 20.2% 25.1%;
    --secondary-foreground: 0 0% 100%;
    --muted: 215 20.2% 25.1%;
    --muted-foreground: 215 20.2% 65.1%;
    --accent: 215 20.2% 25.1%;
    --accent-foreground: 0 0% 100%;
    --destructive: 0 84.2% 60.2%;
    --destructive-foreground: 0 0% 100%;
    --border: 215 20.2% 25.1%;
    --input: 215 20.2% 25.1%;
    --ring: 183 53% 34%;
  }
}

@layer base {
  * {
    @apply border-border;
  }
  body {
    @apply bg-background text-foreground;
  }
}

/* Custom styles for react-big-calendar with Tailwind */
@layer components {
  /* General Calendar Wrapper */
  .rbc-calendar {
    @apply border rounded-md p-4 bg-card text-card-foreground;
    height: 700px; /* Explicit height for the calendar */
    display: flex;
    flex-direction: column;
  }

  /* Toolbar - We use a custom one, but just in case */
  .rbc-toolbar {
    @apply flex justify-between items-center mb-4;
  }
  .rbc-toolbar .rbc-toolbar-label {
    @apply text-xl font-bold;
  }
  .rbc-btn-group button {
    @apply px-3 py-1 border rounded-md bg-background text-foreground hover:bg-accent;
  }
  .rbc-btn-group button.rbc-active {
    @apply bg-primary text-primary-foreground;
  }

  /* Main View Containers */
  .rbc-month-view, .rbc-time-view, .rbc-agenda-view {
    @apply flex-grow;
    display: flex;
    flex-direction: column;
  }

  /* Headers */
  .rbc-header {
    @apply text-center p-2 border-b font-semibold text-muted-foreground;
    flex: 1 0 0%;
  }
  .rbc-time-header {
    @apply flex-none;
  }
  .rbc-time-header-content {
    @apply flex-1 flex flex-col;
  }
  .rbc-time-header .rbc-row.rbc-row-resource {
    @apply flex-none;
  }

  /* Day Backgrounds */
  .rbc-day-bg {
    @apply border-l;
    flex: 1 0 0%;
  }
  .rbc-day-bg:first-child {
    @apply border-l-0;
  }
  .rbc-today {
    @apply bg-accent;
  }

  /* Month View Specifics */
  .rbc-month-view {
    @apply border-t border-r;
  }
  .rbc-month-row {
    @apply flex flex-1 min-h-0;
    border-bottom: 1px solid var(--border);
  }
  .rbc-month-row:last-child {
    border-bottom: none;
  }
  .rbc-month-cell {
    @apply flex-1 p-1 border-b border-l;
    display: flex;
    flex-direction: column;
  }
  .rbc-date-cell {
    @apply text-right text-sm font-medium p-1;
    flex: 1 0 0%;
  }
  .rbc-off-range-bg {
    @apply bg-muted/50;
  }

  /* Events */
  .rbc-event {
    @apply p-1 rounded-md text-white text-sm shadow-sm;
    min-height: 20px;
    cursor: pointer;
  }
  .rbc-event-label {
    @apply font-semibold;
  }
  .rbc-event-content {
    @apply block;
  }

  /* Custom Event Colors via eventPropGetter */
  .rbc-event-custom {
    @apply text-white p-1 rounded-md text-xs border-transparent;
  }
  .rbc-event-incident-own {
    @apply bg-green-600 hover:bg-green-700;
  }
  .rbc-event-incident-other {
    @apply bg-blue-600 hover:bg-blue-700;
  }
  .rbc-event-category-coordinador { @apply bg-orange-500; }
  .rbc-event-category-entrevista { @apply bg-purple-500; }
  .rbc-event-category-activitats { @apply bg-teal-500; }
  .rbc-event-category-reunions { @apply bg-sky-500; }
  .rbc-event-category-jav { @apply bg-lime-500; }
  .rbc-event-category-calendari { @apply bg-yellow-500; }
  .rbc-event-category-csi { @apply bg-red-500; }
  .rbc-event-category-eib { @apply bg-cyan-500; }
  .rbc-event-category-fb { @apply bg-indigo-500; }
  .rbc-event-category-proves { @apply bg-pink-500; }

  /* Week/Day View Specifics */
  .rbc-time-content {
    @apply flex-1 flex;
    border-top: 1px solid var(--border);
    overflow-y: auto;
  }
  .rbc-time-gutter, .rbc-header-gutter {
    @apply flex-none;
  }
  .rbc-day-slot {
    @apply flex-1;
  }
  .rbc-timeslot-group {
    @apply flex-1;
  }
  .rbc-time-slot {
    @apply border-b border-l;
    height: 40px; /* Adjust as needed */
  }
  .rbc-time-slot:last-child {
    border-bottom: none;
  }
  .rbc-label {
    @apply text-xs text-muted-foreground;
  }

  /* Agenda View */
  .rbc-agenda-view table {
    @apply w-full border-collapse;
  }
  .rbc-agenda-view .rbc-agenda-date-cell {
    @apply p-2 font-semibold border-b;
  }
  .rbc-agenda-view .rbc-agenda-time-cell {
    @apply p-2 text-muted-foreground;
  }
  .rbc-agenda-view .rbc-agenda-event-cell {
    @apply p-2 border-b;
  }

  /* "Show More" button */
  .rbc-show-more {
      @apply text-primary font-semibold text-xs cursor-pointer hover:underline;
  }
}